.section {
  position: relative;
  &__title {
    text-align: center;
    margin-bottom: 100px;
    @include xl {
      margin-bottom: 80px; }
    @include lg {
      margin-bottom: 60px; }
    @include md {
      margin-bottom: 40px; }
    @include sm {
      margin-bottom: 20px; } }
  &__title-block {
    margin-bottom: 50px;
    text-align: center;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__btn {
    padding-top: 30px;
    text-align: center;
    @include md {
      padding-top: 15px; } }

  &--gray-lightest {
    background-color: $gray-lightest; }
  &--bg {
    @include cover; } }


.section-half-full {
  $this: &;
  position: relative;
  &__left {
    position: absolute;
    width: 50%;
    top: 0;
    bottom: 0;
    left: 0;
    @include md {
      width: auto;
      position: static; } }
  &__right-in {
    margin-left: 50%;
    @include md {
      margin-left: 0; } }
  &__left-in {
    margin-right: 50%;
    @include md {
      margin-right: 0; } }
  &--right {
    #{$this}__left {
      position: relative;
      // width: 50%
      width: auto;
      left: auto;
      bottom: auto;
      top: auto; }
    #{$this}__right {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 50%;
      @include md {
        width: auto;
        position: static; } } } }

.s-solutions {
  &__img {
    @include cover;
    padding-top: 60%;
    margin-bottom: 30px; }
  &__link {
    text-align: center; }
  &__right {
 }    // margin-left: -15px
  &__text-block {
    padding-left: percentage(1/6);
    margin-bottom: 85px;
    @include xl {
      padding-left: 15px;
      margin-bottom: 60px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 20px; } } }

.s-about {
  color: $white;
  overflow: hidden;
  padding-bottom: 90px;
  @include xl {
    padding-bottom: 45px; }
  @include md {
    padding-bottom: 0; }
  &__inner {
    @include flex;
    @include md {
      display: block; } }
  &__left {
    width: 50%;
    @include md {
      width: auto;
      margin-left: -15px;
      margin-right: -15px; } }
  &__right {
    width: 50%;
    @include md {
      width: auto;
      margin-left: -15px;
      margin-right: -15px; } }
  &__img {
    height: 100%;
    margin-right: -75px;
    margin-top: 90px;
    @include cover;
    @include xl {
      margin-top: 45px;
      margin-right: 0; }
    @include md {
      margin-top: 0;
      padding-top: 50%; }
    @include xs {
      padding-top: 70%; } }
  &__content {
    background-color: $accent;
    position: relative;
    padding-top: 75px;
    padding-bottom: 75px;
    padding-right: percentage(1/6);
    @include xl {
      padding-top: 50px;
      padding-bottom: 50px; }
    @include lg {
      padding-top: 40px;
      padding-bottom: 40px;
      padding-right: 15px; }
    @include md {
      padding-left: 15px; }
    @include before {
      position: absolute;
      top: 0;
      bottom: 0;
      background-color: $accent;
      width: 50vw;
      right: 100%;
      @include md {
        display: none; } } } }

.s-contacts {
  overflow: hidden;
  &__content {
    padding-top: 20%;
    padding-bottom: 20%;
    background-color: $accent;
    color: $white;
    position: relative;
    @include md {
      padding-top: 10%;
      padding-bottom: 10%;
      padding-left: 15px;
      padding-right: 15px;
      margin-left: -15px;
      margin-right: -15px;
      @include before {
        display: none; } }


    @include before {
      background-color: $accent;
      top: 0;
      bottom: 0;
      right: 100%;
      width: 50vw;
      position: absolute; } } }

.s-showreel {
  color: $white;
  &__inner {
    position: relative;
    padding-top: 40%;
    @include sm {
      padding-top: 60%; }
    @include xs {
      padding-top: 90%; } }
  &__btn {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    right: percentage(1/12); } }
